/**
 * 
 */
import React from "react";
import { memo } from "react";
import "./SignalTree.css";
import vehicleApi from "./VssAPI.json";

class VssTree extends React.Component {

	selectedListItem = null;
	oldPath = null;
	
	constructor(props) {
        super(props);
		this.state = {
			path: "Vehicle",
			type: vehicleApi.Vehicle.type ? vehicleApi.Vehicle.type : "-",
			description: vehicleApi.Vehicle.description ? vehicleApi.Vehicle.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: vehicleApi.Vehicle.comment ? vehicleApi.Vehicle.comment : "-",
			deprecation: vehicleApi.Vehicle.deprecation ? vehicleApi.Vehicle.deprecation : "-",
			allowed: "-"
		};
    }

	handleTreeEvent(treeItem, node, path) {
		if (path !== 'Vehicle') {
			treeItem.parentElement.querySelector(".nested").classList.toggle("active");
			treeItem.classList.toggle("caret-down");
		}
		
		this.setState({
			path: path,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: "-"
		});

		if (this.selectedListItem != null) {
			if (this.oldPath !== 'Vehicle') {
				this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
			}
			else {
				this.selectedListItem.setAttribute("style", "color: black; font-size: 22px; cursor: pointer;");
			}
		}

		if (path !== 'Vehicle') {
			treeItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		}
		else {
			treeItem.setAttribute("style", "color: blue; font-size: 22px; font-weight: 700; cursor: pointer;");
		}

		this.selectedListItem = treeItem;
		this.oldPath = path;
	}

	getList(node, path) {
		const children = node['children'];
		return (
			<ul>
				{Object.keys(children).map((key) =>
					this.getSubTree(children[key], path + "." + key, key)
				)}
			</ul>
		);
	}

	getSignalCount(node) {
		let size = 0;
		const children = node['children'];
		if (children) {
			Object.values(children).forEach((subnode) => {
				size += this.getSignalCount(subnode);
			});
		}
		else {
			size = 1;
		}
		return size;
	}

	getSubTree(node, path, name) {
		const children = node['children'];
		return (
			<li key={path}>
				{node['type'] === 'branch' ?
					<>
						<span className="caret" onClick={(e) => this.handleTreeEvent(e.target, node, path)} >
							{name}
						</span>
						<span>
							&nbsp;({this.getSignalCount(node)})
						</span>
						<ul className="nested">
							{Object.keys(children).map((key) =>
								this.getSubTree(children[key], path + "." + key, key)
							)}
						</ul>
					</>
					:
					<>
						<span onClick={(e) => this.handleSignal(e.target, node, path)} style={{ cursor: "pointer" }} >
							{name}
						</span>
					</>
				}
			</li>
		);
	}

	handleSignal(listItem, signal, path) {
		this.setState({
			path: path,
			type: signal.type ? signal.type : "-",
			description: signal.description ? signal.description : "-",
			datatype: signal.datatype ? signal.datatype : "-",
			default: signal.default ? signal.default : "-",
			min: signal.min ? signal.min : "-",
			max: signal.max ? signal.max : "-",
			unit: signal.unit ? signal.unit : "-",
			comment: signal.comment ? signal.comment : "-",
			deprecation: signal.deprecation ? signal.deprecation : "-",
			allowed: signal.allowed ? signal.allowed : "-"
		});

		if (this.selectedListItem != null) {
			this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
		}
		listItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		this.selectedListItem = listItem;
	}

	getAllowedList() {
		if (this.state.allowed === "-") {
			return "-";
		}
		else {
			if (this.state.allowed != null && this.state.allowed.length !== 0) {
				return (
					<select style={{width: "100%"}} >
						{this.state.allowed.map((label) => 
							<option key={label}>{label}</option>
						)}
					</select>
				);
			}
			else {
				return "-";
			}
		}
	}
   
	render() {
		return (
			<div className="w3-row-padding w3-margin-top">
				<div className="w3-col w3-container" style={{ width: "40%" }}>
					<ul id="myUL">
						<li key={"Vehicle"} >
							<span
								onClick={(e) => this.handleTreeEvent(e.target, vehicleApi.Vehicle, "Vehicle")}
								style={{ cursor: "pointer", fontSize: "22px" }}
							>
								{"\u25B6"}{"Vehicle"}
							</span>
							<span>
								&nbsp;({this.getSignalCount(vehicleApi.Vehicle)})
							</span>
							{this.getList(vehicleApi.Vehicle, "Vehicle")}
						</li>
					</ul>
				</div>
				<div className="w3-col w3-container MyStckyHeader" style={{width:"60%"}}>
					<div className="w3-card-4">
						<header className="w3-container w3-blue">
							<h2>{this.state.path}</h2>
						</header>

						<div className="w3-container">
							<table className="w3-table">
								<tbody>
								<tr>
									<td><b>path</b></td>
									<td><b>{this.state.path}</b></td>
								</tr>
								<tr>
									<td><b>description</b></td>
									<td>{this.state.description}</td>
								</tr>
								<tr>
									<td><b>type</b></td>
									<td>{this.state.type}</td>
								</tr>
								<tr>
									<td><b>datatype</b></td>
									<td>{this.state.datatype}</td>
								</tr>
								<tr>
									<td><b>default</b></td>
									<td>{this.state.default}</td>
								</tr>
								<tr>
									<td><b>min</b></td>
									<td>{this.state.min}</td>
								</tr>
								<tr>
									<td><b>max</b></td>
									<td>{this.state.max}</td>
								</tr>
								<tr>
									<td><b>unit</b></td>
									<td>{this.state.unit}</td>
								</tr>
								<tr>
									<td><b>allowed</b></td>
									<td>{this.getAllowedList()}</td>
								</tr>
								<tr>
									<td><b>comment</b></td>
									<td>{this.state.comment}</td>
								</tr>
								<tr>
									<td><b>deprecation</b></td>
									<td>{this.state.deprecation}</td>
								</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default memo(VssTree);